$primary-color: #673AB7;
$dark-primary-color: #311b92;
$light-primary-color: #D1C4E9;
$accent-color: #FFC107;
$primary-text: #212121;
$secondary-text: #757575;
$break-tablet: 767px;
$break-comp: 990px;

html, body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}
body {
  background-color: #F0F0F0;
}

img {max-width: 100%;}
.container, main {
  width: 100%;max-width: 990px;margin: 0 auto;padding-left: 15px;padding-right:15px;box-sizing:border-box;
  @media screen and (min-width: $break-comp) {
    padding-left: 30px;
    padding-right: 30px;
  }
}

header {
  background-color: $primary-color;
  color: white;
  //padding: 15px;
   .header-container {
    // display: flex;
    // justify-content: space-between;
    // align-items: center;
    padding: 12px 0;
    background-color: $dark-primary-color;
    h1 {
      margin:0;
      font-size: 28px;
      //font-weight: 400;
    }
   }

   nav {
    background-color: $primary-color;
    color: white !important;
    .nav-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding:6px 15px;
      box-sizing: border-box;
    }
    ul {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      list-style: none;
      color: white;
      padding:0;
      margin:0;
      li {
        margin-right: 10px;
        display: block;
        padding: 4px 8px;
        border-radius: 4px;
        transition: all 200ms ease-in-out;
      }
      li.selected {
        background-color: $dark-primary-color;
      }
    }
    a {color: white;}
  }
}

main {
  padding: 10px 15px;
  .preloader-container {width: 100%;text-align: center;}
  .investigadores {
    .investigador-container {
      width: 100%;
      margin: 20px auto;
      box-shadow: 0 0px 4px rgba(0,0,0,0.2);
      padding: 10px;
      box-sizing: border-box;
      border-radius: 8px;
      display:flex;
      align-items: center;
      justify-content: flex-start;
      background-color: #FFF;
      .foto {
        width: 80px;
        min-width: 80px !important;
        height: 80px;
        border-radius: 50%;
        overflow: hidden;
        margin: 0;
        background-color: #ccc;
      }
      .info {
        padding-left:15px;
        h2 {margin-top:5px;margin-bottom:0;font-size: 18px;}
        p {color: $secondary-text;margin-top:5px;margin-bottom:5px;}

      }
    }
    @media screen and (min-width: $break-tablet) {
      display:flex;

      .investigador-container {
        margin: 10px;
      }
    }
  }
}
