$primary-color: #673AB7;
$dark-primary-color: #311b92;
$light-primary-color: #D1C4E9;
$accent-color: #FFC107;
$primary-text: #212121;
$secondary-text: #757575;
$break-tablet: 767px;
$break-comp: 990px;

.back-arrow {
	display: flex;
	align-items: center;
	color: $secondary-text;
	margin-bottom: 20px;
	font-size: 14px !important;
	cursor: pointer;
}

.card {
	background-color: #FFF;
  box-shadow: 0 0px 4px rgba(0,0,0,0.2);
  padding: 15px;
  box-sizing:border-box;
  border-radius: 8px;
  margin-bottom: 15px;

  h3 {margin-top: 0;}
  article {
  	border-left: 2px solid #E8E8E8;
  	padding-left: 8px;
  	margin-bottom: 15px;
  	p {
  		margin-top:0;
  		margin-bottom: 2px;
  		line-height: 1.2;

  		&.grey {color: $secondary-text;}
  		&.small {font-size: 13px;}
  	}
  }
}

.investigador-header {
	display:flex;
	align-items: center;
	justify-content: flex-start;
	.foto {
		width: 140px;
		height: 140px;
		border-radius: 50%;
		overflow: hidden;
		border: 1px solid #ccc;
	}
	.info {
		padding-left: 15px;
	}
}